<script>
export default {
  name: "table-component",
  props: ["data", "tableColumns"],
  data() {
    return {
      api: this.apiUrl,

      loading: true,
    };
  },
  methods: {
    selectChangeHandler(item) {
      this.$emit("selectionChange", item);
    },
    downloadPdf(item) {
      const fileId = item.file_Id;
      this.$emit("downloadPdfHandler", fileId);
      this.$emit("downloadPdfHandler2", item);
    },

    // table
    getRowKeys(row) {
      if (row.index) {
        return row.index;
      } else {
        return row.id;
      }
    },
    rowStyleHandler(row) {
      if (row.row.isWeekend !== "1" && row.row.isNight !== "1") return;
      return "fontColorBlue";
    },
  },
  mounted() {
    if (this.data) this.loading = false;
  },
};
</script>

<template src="./Table.html"></template>
<style lang="sass" scoped src="./Table.sass"></style>
