      default: "檔案格式為PDF，在2MB以內",
    },
    uploadAction: {
      type: [String],
      default: configSetting.apiUrl,
    },
    limitNumber: {
      type: Number,
      default: 1,
    },
  },
  methods: {
    changeHandler(file, fileList) {
      this.$emit("fileChangeHandler", fileList);
    },
    removeHandler(file, fileList) {
      this.$emit("fileRemoveHandler", fileList);
    },
  },
  mounted() {},
};
</script>

