<script>
import UploadComponent from "@/components/Upload/Upload.vue";
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
export default {
  name: "newAnnouncementReleasedComponent",
  components: { UploadComponent, TableComponent, PaginationComponent },
  data() {
    return {
      form: {
        Bbs_No: "",
        Title: "",
        Content: "",
        validDate: [],
        Vaild_Beg_Date: "",
        Vaild_End_Date: "",
        Bbs_Cat: "0",
        Location_Id: "",
        File: File,
        IsEmail: "0",
      },
      rules: {
        Title: [{ required: true, message: "必填", trigger: "blur" }],
        Content: [{ required: true, message: "必填", trigger: "blur" }],
        validDate: [{ required: true, message: "必填", trigger: "change" }],
        IsEmail: [{ required: true, message: "必填", trigger: "change" }],
        Location_Id: [{ required: true, message: "必填", trigger: "change" }],
      },
      tableColumns: [
        { id: "index", width: "70", label: "項次" },
        { id: "col1", width: "", label: "工作流水號/管理身份" },
        { id: "col2", width: "", label: "管理者工號/姓名/部門/分機" },
        { id: "col3", width: "", label: "建立日期(開始~結束日期)" },
        { id: "title", width: "", label: "訊息標題" },
        { id: "col4", width: "", label: "訊息分類/所屬廠區" },
      ],
      data: [],
      bbsList: [],
      locations: [],
      tableList: [],
      info: {},
    };
  },
  computed: {
    dataList() {
      const data = this.data;
      data.forEach((e, index) => {
        e.index = index + 1;
        e.col1 = `${e.bbs_No}/${e.userGroup}`;
        e.col2 = `${e.emp_Name}(ext=${e.emp_Extension_No})
/${e.emp_Dept_Full_Name}`;
        e.col3 = `${e.issue_Date}/(${e.valid_Beg_Date}~${e.valid_End_Date})`;
        e.col4 = `${e.bbs_Cat === "REGULAR" ? "一般公告" : "各廠區施工規範"}/${
          e.description || ""
        }`;
      });
      return data;
    },
  },
  methods: {
    // API取得資料
    getBBS() {
      this.$store
        .dispatch("QryBBS")
        .then((res) => {
          const data = res.data.data;
          this.locations = data.locations;
          this.data = data.bbs;
          this.tableList = this.dataList.slice(0, 10);
          this.info = data.info;
          this.$store.state.currentPage = 1;
        })
        .catch((err) => console.log(err));
    },
    // API新增資料
    addBBS(data) {
      const formData = this.changeFormData(data);
      this.$store
        .dispatch("InsBBS", formData)
        .then((res) => {
          if (res.data.isOK) {
            this.getBBS();
            this.$store.state.currentPage = 1;
            this.resetForm("form");
            this.$message({
              type: "success",
              duration: "5000",
              showClose: true,
              message: "新增成功",
            });
          }
        })
        .catch((err) => console.log(err));
    },
    // 上傳檔案
    fileHandleChange(file) {
      // 1MB = 1 * 1024 * 1024;
      const fileSize = file[0].size > 3 * 1024 * 1024;
      const fileFormat = file[0].raw.type;
      const isPdf = fileFormat === "application/pdf";
      const isPng = fileFormat === "image/png";
      const isJpg = fileFormat === "image/jpeg";
      if (fileSize || (!isPdf && !isPng && !isJpg)) {
        this.$message({
          type: "error",
          duration: 5000,
          showClose: true,
          message: "檔案不可大於3MB且檔案格式必須為 PDF、JPG、PNG",
        });
        this.$refs.uploadComponent.$refs.Upload.clearFiles();
        return false;
      } else {
        this.form.File = file[0].raw;
        return true;
      }
    },
    // 移除檔案
    fileHandleRemove(file) {
      this.form.File = file;
    },
    // 分頁操作
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.dataList.slice(cur, last);
    },
    // 清除表單
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.$refs.uploadComponent.$refs.Upload.clearFiles();
    },
    // 送出表單
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.form.Vaild_Beg_Date = this.form.validDate[0];
          this.form.Vaild_End_Date = this.form.validDate[1];
          this.addBBS(this.form);
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    editHandler(item) {
      const obj = {
        locations: this.locations,
      };
      const data = { ...item, ...this.info, ...obj };
      this.$router.push({
        name: "newAnnouncementReleasedEdit",
        query: { data: JSON.stringify(data) },
      });
    },
    deleteHandler(item) {
      const data = {
        Bbs_No: item.bbs_No,
        Title: item.title,
        Content: item.content,
        Vaild_Beg_Date: item.valid_Beg_Date,
        Vaild_End_Date: item.valid_End_Date,
        Bbs_Cat: item.bbs_Cat === "FAB" ? "1" : "0",
        IsEmail: 0,
        Location_Id: item.location_Id,
        File: null,
      };
      this.$store
        .dispatch("DeleteBbsNotify", data)
        .then((res) => {
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: "5000",
              showClose: true,
              message: "刪除成功",
            });
            this.getBBS();
          }
        })
        .catch((err) => console.log(err));
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    this.getBBS();
    this.$store.state.currentPage = 1;
  },
};
</script>

<template src="./newAnnouncementReleased.html"></template>
